import React, { useState } from "react";
import { Link } from "react-router-dom";
import { SectionTitle } from "../../components";
import images from "../../assets/images"
import "./Prints.css";

const Procedure = () => {
    const { printingDepartment } = images;

    const [showText1, setShowText1] = useState(true);
    const [showText2, setShowText2] = useState(false);
    const [showText3, setShowText3] = useState(false);
    const [showText4, setShowText4] = useState(false);

    const toggleText = (textNumber) => {
        switch (textNumber) {
            case 1:
                setShowText1(true);
                setShowText2(false);
                setShowText3(false);
                setShowText4(false);
                break;
            case 2:
                setShowText1(false);
                setShowText2(true);
                setShowText3(false);
                setShowText4(false);
                break;
            case 3:
                setShowText1(false);
                setShowText2(false);
                setShowText3(true);
                setShowText4(false);
                break;
            case 4:
                setShowText1(false);
                setShowText2(false);
                setShowText3(false);
                setShowText4(true);
                break;
            default:
                break;
        }
    };

    return (
        <div className="prints" id="prints">
            <SectionTitle startText={"   "} />
            <section className="prints__content">
                <div className="content__text">
                    <h2>
                                     .
                    </h2>

                    <div onClick={() => toggleText(1)}>
                        <h3 className="prints__subtitle">    <i className="ri-corner-down-left-fill"></i></h3>
                        <p>
                            {showText1 && (
                                <>             ,   , ,   .      ,       .</>
                            )}
                        </p>
                    </div>

                    <div onClick={() => toggleText(2)}>
                        <h3 className="prints__subtitle">    <i className="ri-corner-down-left-fill"></i></h3>
                        <p>
                            {showText2 && (
                                <>           .   ,    , -   ,       .</>
                            )}
                        </p>
                    </div>

                    <div onClick={() => toggleText(3)}>
                        <h3 className="prints__subtitle">  <i className="ri-corner-down-left-fill"></i></h3>
                        <p>
                            {showText3 && (
                                <>               .         ,     .</>
                            )}
                        </p>
                    </div>

                    <div onClick={() => toggleText(4)}>
                        <h3 className="prints__subtitle">     <i className="ri-corner-down-left-fill"></i></h3>
                        <p>
                            {showText4 && (
                                <>   ,           .              .</>
                            )}
                        </p>
                    </div>
                    
                    <div className="products__catalog">
                        <Link to="/prints">
                            <button className="button"></button>
                        </Link>
                    </div>
                </div>
                <div className="content__img">
                    <img
                    className={`content__img-img`}
                    src={printingDepartment}
                    alt="printing department"/>
                </div>
            </section>
        </div>
    );
};

export default Procedure;
